import {gql} from 'graphql-tag'; 

//   OrderStatusID 
//     1- 
//     2 -  
//     3-   
// 

export const MUTATE_ORDER_STATUS = gql`
    mutation MyMutation($OrderID: Int!, $AcceptanceDate: timestamptz!){
    update_erp_Orders_by_pk(
      pk_columns: {OrderID: $OrderID},
       _set: {
         OrderStatusID: 2,
         AcceptanceDate: $AcceptanceDate,
         }
      ) {
        OrderID
    }
  }

`;