import gql from 'graphql-tag'; 

//          
//   OrderStatusID 
//     1- 
//     2 -  
//     3-   
// 

export const GetOrdersSubscription = gql`
subscription{
    erp_Orders(where: {OrderStatusID: {_neq: 3} }) {
      OrderID
      Entity
      InvoiceNumber
      City
      ShippingDate
      AcceptanceDate
      PaidAmount
      TotalAmount
      AwaitingDispatch
      CreatingDate
      ManagerID
      OrderStatus {
        Name
        ID
      }
      OrderItems(order_by: {OrderItemID: asc}){
        Quantity
        OrderItemID
        Name
        OrderID
      } 
      User {
        FirstName
        LastName
      }

    }  
  }
`
